.gallery {
  max-width: 90%;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 350px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

body {
  color: #212121;
  background-color: #a8a8d6;
  margin: 0;
  font-family: Poppins, sans-serif;
  font-size: 14px;
  line-height: 1.5;
}

a {
  color: #000;
  background-color: #fff;
  border-radius: 4px;
  padding: 10px;
  text-decoration: none;
  display: block;
}

.form-input {
  height: 30px;
  border-radius: 10px;
  padding: 5px;
  font-size: 20px;
}

.btn-submit, .load-more {
  height: 40px;
  border-radius: 10px;
  padding: 5px;
  font-size: 20px;
}

.form-conteiner, .container-load-more {
  background-color: #d2691e;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 10px;
  list-style: none;
  display: flex;
  box-shadow: 0 2px 1px -1px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12);
}

.photo-card {
  width: 100%;
  align-self: center;
}

.info {
  height: 25%;
  width: auto;
  text-align: center;
  flex-direction: row;
  gap: 15px;
  display: flex;
}

.gallery-image:hover {
  transform: scale(1.03);
}

.gallery-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.container-load-more {
  justify-content: center;
  margin-bottom: 50px;
  display: none;
}

